import { Helmet } from 'react-helmet-async';
// import { faker } from '@faker-js/faker';
// @mui
import { Component, forwardRef, useState, useEffect } from "react";
import { useTheme } from '@mui/material/styles';
import { Grid, Container, Typography } from '@mui/material';
// components
import axios from 'axios';
import Iconify from '../components/iconify';
// sections
import {
  AppTasks,
  AppNewsUpdate,
  AppOrderTimeline,
  AppCurrentVisits,
  AppWebsiteVisits,
  AppTrafficBySite,
  AppWidgetSummary,
  AppCurrentSubject,
  AppConversionRates,
} from '../sections/@dashboard/app';

import { variables } from "../variables";

// ----------------------------------------------------------------------

export default function DashboardAppPage() {
  const theme = useTheme();
  const [rows, setRows] = useState([]);
  const [proposed, setProposed] = useState([]);
  const [declined, setDeclined] = useState([]);
  const [pending, setPending] = useState([]);
  const [executed, setExecuted] = useState([]);
  // const [rows, setRows] = useState([]);

  const getApproved = async () => {
    try {
      const Endpoint = `Approved/Approved`
      const data = await axios.get(variables.API_URL + Endpoint);

      setRows(data.data);


    }
    catch (e) {
      console.log(e);
    }
  }


  const getProposed = async () => {
    try {
      const Endpoint = `Approved/Proposed`
      const data = await axios.get(variables.API_URL + Endpoint);

      setProposed(data.data);


    }
    catch (e) {
      console.log(e);
    }
  }

  const getPending = async () => {
    try {
      const Endpoint = `Approved/Pending`
      const data = await axios.get(variables.API_URL + Endpoint);

      setPending(data.data);


    }
    catch (e) {
      console.log(e);
    }
  }


  const getExecuted = async () => {
    try {
      const Endpoint = `Approved/Executed`
      const data = await axios.get(variables.API_URL + Endpoint);

      setExecuted(data.data);


    }
    catch (e) {
      console.log(e);
    }
  }

  const getDeclined = async () => {
    try {
      const Endpoint = `Approved/Declined`
      const data = await axios.get(variables.API_URL + Endpoint);

      setDeclined(data.data);


    }
    catch (e) {
      console.log(e);
    }
  }

  // Displaying Data

  useEffect(() => {
    getApproved();
    getProposed();
    getPending();
    getExecuted();
    getDeclined();
  }, []);

 
  return (
    <>
      <Helmet>
        <title> East African Bank </title>
      </Helmet>

      <Container maxWidth="xl">
        <Typography variant="h4" sx={{ mb: 5 }}>
          Hi, Welcome back
        </Typography>

        <Grid container spacing={3}>
          <Grid item xs={12} sm={6} md={3}>
            <AppWidgetSummary title="Total Number of Approved Applications" total={rows.length} icon={'mdi:people-group'} />
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <AppWidgetSummary title="Proposed Applications" total={proposed.length} color="info" icon={'mdi:people-group'} />
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <AppWidgetSummary title="Pending Applications" total={pending.length} color="warning" icon={'eos-icons:job'} />
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <AppWidgetSummary title="Decline Applications" total={declined.length} color="error" icon={'mdi:people-group'} />
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <AppWidgetSummary title="Total Number of executed Applications" total={executed.length} icon={'mdi:people-group'} />
          </Grid>
<br/>
          <Grid item xs={12} md={6} lg={4}>
            
            <AppCurrentVisits
              title="Applications status"
              chartData={[
                { label: 'Approved', value: rows.length },
                { label: 'Declined', value: declined.length },
                { label: 'Executed', value: executed.length },
                
              ]}
              chartColors={[
                theme.palette.primary.main,
                theme.palette.error.main,
                theme.palette.info.main,
                theme.palette.warning.main,
               
              ]}
            />
          </Grid>

     
       


     
        </Grid>
      </Container>
    </>
  );
}
